.blog-post-card {
    -ms-flex: 1 1;
        flex: 1 1;
    margin: 10px 0;
    -webkit-box-shadow: 2px 2px 5px #7e7e7e;
            box-shadow: 2px 2px 5px #7e7e7e;
    /* background-color: #f5f5f5; */
    background-color: #000000;
    color: #504f4f;
    cursor: pointer;
}

.blog-post-card:nth-child(2n) {
    -webkit-transform: rotate(0.5deg);
        -ms-transform: rotate(0.5deg);
            transform: rotate(0.5deg);
}

.blog-post-card:nth-child(2n + 1) {
    -webkit-transform: rotate(-0.5deg);
        -ms-transform: rotate(-0.5deg);
            transform: rotate(-0.5deg);
}

.blog-post-card:hover {
    color: black;
    background-color: #f9f9f9;    
    -webkit-box-shadow: 3px 3px 5px #b1b1b1;    
            box-shadow: 3px 3px 5px #b1b1b1;    
}

.blog-post-card .blog-post-card-content {
    position: relative;
}

.blog-post-card .blog-post-card-content p {
    padding: 12px 12px 25px 12px;
    margin: 0;
}

.blog-post-card .blog-post-card-content span {
    position: absolute;
    bottom: 5px;
    right: 10px;
    font-size: 80%;
    color: #696666
}

.blog-post-card:hover .blog-post-card-content h3 {
    text-decoration: underline;
}

.blog-post-card-content-title-container {
    background-color: #000000;
    padding: 1px;
}

.blog-post-card-content--title {
    color: #fff;
    margin: 20px;
}

.blog-post-card__admin {
    display: -ms-flexbox;
    display: flex;
    -webkit-transform: none !important;
        -ms-transform: none !important;
            transform: none !important;
    padding: 10px;
}

.blog-post-card__admin .blog-post-card-content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.blog-post-card__admin .blog-post-card-content .blog-post-card-content-title-container {
    background-color: inherit;
}

.blog-post-card__admin .blog-post-card-content .blog-post-card-content--title {
    color: black;
}

.blog-post-card__admin .blog-post-card-content p {
    margin-bottom: 5px;
}

.blog-post-card__admin .blog-post-card-controls {
    -ms-flex: 0 0 50px;
        flex: 0 0 50px;
    padding: 15px; 
}

.blog-post-card__admin .blog-post-card-controls input {
    width: 100%;
    
}

.blog-post-card__admin .blog-post-card-controls input.delete {
    background-color: #ff7171;
}

.blog-post-card__admin .blog-post-card-controls input.cancel {
    background-color: #ffbd71;
}

.blog-post-card__admin .blog-post-card-content textarea {
    height: 350px;
}

.blog-post-card__admin .blog-post-card-content input, .blog-post-card__admin .blog-post-card-content textarea {
    display: block;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    resize: vertical;
}

.blog-post-card-content__edit-header {
    border-bottom: 2px solid black;
    margin-top: 20px;
}


@media (min-width: 1000px) {
    .blog-post-card {
        -ms-flex: 1 1 45%;
            flex: 1 1 45%;
        margin-right: 10px;
        -webkit-transition: all 1s ease-in;
        -o-transition: all 1s ease-in;
        transition: all 1s ease-in;
    }

    .blog-post-card.blog-post-card__admin-edit {
        max-width: 90%;
        -ms-flex: 1 1 100%;
            flex: 1 1 100%;
    }
}

@media (min-width: 1280px) {
    .blog-post-card {
        -ms-flex: 1 0 30%;
            flex: 1 0 30%;
        max-width: 30%;
    }
}


