.header {
    background-color: #383838;
    color: #eeeeee;
    padding: 80px 20px;
    background-image: url('../resources/zebra_pattern.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.header--title {
    font-size: 3em;
    font-weight: bolder;
    text-align: center;
    margin: 0;
    border-bottom: 2px solid white;
}

.header-backdrop {
    background-color: rgba(0, 0, 0, 0.8);
    padding: 30px;
}

@media (min-width: 1000px) {
    .header {
        padding: 90px;
    }
}



